import { fryj, ssw1, ssw3, xca3, xcb3, zj, zx1, zx2 } from '../address'
import {
  FL,
  HFS,
  HLG,
  JG,
  LM,
  LWC,
  PPG,
  QX,
  WG,
  XG,
  XH,
  XJ,
  XL,
  YJM,
  YT,
} from '../fruit'
import newf from '../newf'
import zao from '../zao'

const t1 = [
  {
    name: '',
    address: zx1,
    fruit: [newf(PPG, 49)],
  },
  {
    name: '',
    address: zx1,
    fruit: [newf(XG, 53), newf(XJ, 45), newf(XJ, 13.5), newf(HFS, 11)],
  },
  {
    name: 'yadong',
    address: zj,
    fruit: [newf(XJ, 12.9)],
  },
  {
    name: '',
    address: zj,
    fruit: [newf(XJ, 11.6)],
  },
  {
    name: '',
    address: zj,
    fruit: [newf(WG, 35)],
  },
  zao('', zj, [[JG], [XL, 19]]),
  zao('', zx2, [[YT, 35]]),
  zao('0330weekly', zj, [[HFS, 27]]),
  zao('', zj, [[LWC, 24], [YJM, 16.5]]),
  zao('John', zj, [[LWC, 24], [HFS, 27]]),
  zao('', zj, [[LWC, 24]]),
  zao('', zx1, [[LWC, 24], [XH, 31]]),
  zao('', zj, [[WG, 35]]),
  zao('', zx2, [[YT, 35]]),
  zao('', zx1, [[HFS, 16.6]]),
  zao('', zx2, [[XJ, 20], [HFS, 11.6], [LM, 26]]),
]

const t2 = [
  zao('', ssw3, [[PPG, 49]]),
  zao('32110', fryj, [[PPG, 49], [WG, 35]]),
  zao('', ssw3, [[FL, 26]]),
  zao('', ssw3, [[HLG, 18]]),
  zao('', ssw3, [[YJM, 8.4], [XH, 30.3]]),
  zao('', ssw1, [[HFS, 27], [LM, 26 * 2], [QX, 18]]),
  zao('', ssw1, [[HLG, 18], [HFS, 27]]),
]

const t3 = [
  zao('', xca3, [[FL, 26]]),
  zao('', xcb3, [[HFS, 27]]),
]

export default [].concat(t1, t2, t3)



